import React, { useState } from "react";
import { Link } from "react-router-dom";

function Airport() {
    const [activeTab, setActiveTab] = useState("departures");

    const handleDeparturesClick = () => {
        setActiveTab("departures");};

    const handleArrivalsClick = () => {
        setActiveTab("arrivals");};

  return (
    <div>
        <div style={{display: "flex", justifyContent:"center", alignItems: "center", fontSize: "28px",margin:"40px  0px 0px 0px"}}>
          <div style={{display: "flex", flexDirection: "column", marginRight: "5%"}}>
              <div style={{display:"flex", alignItems:"flex-end"}}>
                  <img style={{width:"150px",height:"800px",marginTop:"10px",border:"solid", boxShadow: "10px 10px 0px #097138"}} src="/images/TivatFerry.png" alt="" />
              </div>
          </div>
          <div>
          <div style={{color:"white", textAlign: "center", marginBottom: "20px"}}><h2 className="bg-title green">Ferry Lepetane - Kamenari</h2></div>
          <div style={{display:"flex", justifyContent:"center"}}>
              <button
                className="section-button"
                  style={{
                      fontWeight:"bold",
                      background: activeTab === "departures" ? "#097138" : "#EBEBEB",
                      color: activeTab === "departures" ? "white" : "#097138",
                  }}onClick={handleDeparturesClick}>
                  Winter
              </button>
              <button
                className="section-button"
                  style={{
                      fontWeight:"bold",
                      background: activeTab === "arrivals" ? "#097138" : "#EBEBEB",
                      color: activeTab === "arrivals" ? "white" : "#097138",
                  }} onClick={handleArrivalsClick}>
                  Summer
              </button>
          </div>
        {activeTab === "departures" ? (
          <table className="fer-tab" style={{minWidth: "732.5px"}}>
            <tr style={{color: "while" }}>
              <th style={{textAlign: "center"}}>Period</th>
              <th style={{textAlign: "center"}}>Departure time</th>
              <th style={{textAlign: "center"}}>Duration</th>
              <th style={{textAlign: "center"}}>Ferry</th>
              <th style={{textAlign: "center"}}>Status</th>
            </tr>
            <tr className="">
              <th className="air-th" style={{textAlign:"center"}}>00:00<p>-</p>06:00</th>
              <th className="air-th">00:00, 01:00; 02:00;<p>03:00, 04:00, 05:00.</p></th>
              <td className="air-th" rowSpan="2">15 minutes</td>
              <td className="air-th" rowSpan="2">Vasilije</td>
              <th className="air-th">------</th>
            </tr>
            <tr className="air-tr">
              <th className="air-th" style={{textAlign:"center"}}>06:00<p>-</p>00:00</th>
              <th className="air-th">every 15 minutes <p>from 6:00 to 23:45</p></th>
              <th className="air-th">------</th>
          </tr>
          </table>
        ) : (
          <table className="fer-tab" style={{minWidth: "732.5px"}}>
            <tr style={{color: "while" }}>
              <th style={{textAlign: "center"}}>Period</th>
              <th style={{textAlign: "center"}}>Departure time</th>
              <th style={{textAlign: "center"}}>Duration</th>
              <th style={{textAlign: "center"}}>Ferry</th>
              <th style={{textAlign: "center"}}>Status</th>
            </tr>
            <tr className="">
              <th className="air-th" style={{textAlign:"center"}}>00:00<p>-</p>06:00</th>
              <th className="air-th">every 15 minutes <p>from 00:00 to 05:45</p></th>
              <td className="air-th" rowSpan="2">15 minutes</td>
              <td className="air-th" rowSpan="2">Vasilije</td>
              <th className="air-th">------</th>
            </tr>
            <tr className="air-tr">
              <th className="air-th" style={{textAlign:"center"}}>06:00<p>-</p>00:00</th>
              <th className="air-th">every 10 minutes <p>from 06:00 to 23:50</p></th>
              <th className="air-th">------</th>
          </tr>
          </table>)}
          <table style={{margin:"5% auto"}}>
              <tr className="air-row">
                  <th style={{textAlign: "center"}} className="air-cell">Nearest bus stop<img src="/images/icon/busIcon.png"/></th>
                  <th style={{textAlign: "center"}} className="air-cell">Local taxi<img src="/images/icon/taxiIcon.png"/></th>
                  <th style={{textAlign: "center"}} className="air-cell">Nearest airport<img src="/images/icon/airportIcon.png"/></th>
              </tr>
              <tr>
                  <th className="air-cell" style={{textAlign: "center"}}>Lepetane</th>
                  <th className="air-cell" style={{textAlign: "center"}}>RedTaxi</th>
                  <th className="air-cell" style={{textAlign: "center"}}>Tivat (TIV)</th>
              </tr>
          </table>
          <button className="text-button fs-2 fw-bold" style={{marginTop:"8%"}}><Link to="/municipalityDetail/kotor"><span className="navigation-button">&#60;</span> Back to Kotor transport</Link></button>
          </div>
      </div>
    </div>
    )
}

export default Airport