import { Modal, ModalBody, ModalDialog, Button } from "react-bootstrap";
import { useState } from "react";

export default function FollowMe(){

    const [routeHandlerDisplay, setRouteHandlerDisplay] = useState(false);

    const [show, setShow] = useState(false);

    const [value1, setValue1] = useState("");
    const [value2, setValue2] = useState("");

    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);

    const handleDropdownChange = (event) => {
        const { name, value } = event.target;

        if (name === "dropdown1") {
          setValue1(value);
        } else if (name === "dropdown2") {
          setValue2(value);
        }
    };


    function showRouteHandler(){
        setRouteHandlerDisplay(true)
    }

      let imageToShow = null;

      // Conditionally render the image based on dropdown values
      if (value1 === "option1" && value2 === "optionA") {
        imageToShow = <img className="route-image" src="/images/routes/kotor-tivatAirport.png" alt="Image 1" />;
      } else if (value1 === "option1" && value2 === "optionB") {
        imageToShow = <img className="route-image" src="/images/routes/route1.png" alt="Image 2" />;
      } else if (value1 === "option1" && value2 === "optionB") {
        imageToShow = <img className="route-image" src="/images/routes/" alt="Image 2" />;
      } else if (value1 === "option1" && value2 === "optionB") {
        imageToShow = <img className="route-image" src="/images/routes/" alt="Image 2" />;
      } else if (value1 === "option1" && value2 === "optionB") {
        imageToShow = <img className="route-image" src="/images/routes/" alt="Image 2" />;
      } else if (value1 === "option1" && value2 === "optionB") {
        imageToShow = <img className="route-image" src="/images/routes/" alt="Image 2" />;
      } else {
        imageToShow = <p>No image selected</p>;
      }


    return(
        <div className="container" style={{marginTop: "25px"}}>
            { <Modal show={show} onHide={handleClose} closeButton>
            <Modal.Body style={{fontSize: "2rem", height: "25%", width:"auto", display:"flex",flexDirection:"column", textAlign:"center"}}>
                {/* <div>
                <label htmlFor="dropdown1">From:</label>
                <select name="dropdown1" id="dropdown1" onChange={handleDropdownChange}>
                    <option value="">Select an option</option>
                    <option value="option1">Kotor</option>
                    <option value="option2">Tivat</option>
                </select>
                </div>
                <div>
                <label htmlFor="dropdown2">To:</label>
                <select name="dropdown2" id="dropdown2" onChange={handleDropdownChange}>
                    <option value="">Select an option</option>
                    <option value="optionA">AirportTivat</option>
                    <option value="optionB">Mirac</option>
                </select>
                </div>
                {imageToShow} */}
            </Modal.Body>
            </Modal> }
            <div className="route__wrapper" style={{display: routeHandlerDisplay ? "flex" : "none", flexDirection: "column"}}>
                <h1 style={{textAlign: "center"}}>Hello! <span style={{backgroundImage: "linear-gradient(90deg, #facb2f, #82b1aa)", backgroundClip: "text", WebkitBackgroundClip: "text", WebkitTextFillColor: "transparent"}}>Where do you want to be between?</span></h1>
                <div className="route__content" style={{fontSize: "2rem", marginTop: "50px"}}>
                    <div style={{margin: "0 50px"}}>
                        <label htmlFor="dropdown1">From:</label>
                        <select name="dropdown1" id="dropdown1" onChange={handleDropdownChange}>
                            <option value="">Select an option</option>
                            <option value="option1">Kotor</option>
                            <option value="option2">Tivat</option>
                        </select>
                    </div>
                    <div style={{margin: "0 50px"}}>
                        <label htmlFor="dropdown2">To:</label>
                        <select name="dropdown2" id="dropdown2" onChange={handleDropdownChange}>
                            <option value="">Select an option</option>
                            <option value="optionA">AirportTivat</option>
                            <option value="optionB">Mirac</option>
                        </select>
                    </div>
                </div>
                <div className="align-self-center" style={{marginTop: "40px"}}>
                  <div className="d-flex route__content_text" style={{flexDirection:"column"}}>
                      {/*    */}
                      <div style={{maxWidth: "600px", padding: "15px"}}>
                        <h1 style={{textAlign: "center", backgroundColor:"#00575c", borderRadius: "15px", color:"white"}}>By bus</h1>
                        <h5>{imageToShow}</h5>
                        <h2>: max ~ 2 for a trip Kotor - Mirac</h2>
                        <h2>The trip will take 10 - 40 minutes</h2>
                      </div>
                      {/*     */}
                      <div style={{maxWidth: "600px", padding: "15px"}}>
                        <h1 style={{textAlign: "center", backgroundColor: "#ffd000", borderRadius: "15px"}}>By Taxi</h1>
                        <h2>Nontsop</h2>
                        <h2>: From 4.50 to 17.00</h2>
                        <h2>The trip will take 5 - 20 minutes</h2>
                      </div>
                  </div>
                </div>
            </div>
            <div style={{padding: "30px", borderRadius: "15px", fontWeight: "bold", display: routeHandlerDisplay ? "none" : "block"}}>
                <h1 className="display-4" ><span>It's a  <span style={{backgroundImage: "linear-gradient(to right, #ffc508, #ff1808)", padding: "15px", borderRadius: "10px"}}>Route builder</span></span>!</h1>
                <h3 className="follow-me_text" style={{fontSize:"2em", marginTop:"15%"}}>Select your <span style={{backgroundColor: "#9BE0D4", padding: 8, borderRadius: 10, border: "2px solid #82BDB3"}}>departure</span> and <span style={{backgroundColor: "#F7D992", padding: 8, borderRadius: 10, border: "2px solid #C7AE75"}}>arrival</span> points from the drop-down menus and it will analyze all the possibilities to offer you the best route, taking into account the required transfers.</h3>
                <button onClick={showRouteHandler} className="route-builder_button" style={{marginTop:"15%"}}><h2>Start <h1 style={{display: "inline-block", transform: "translateY(8px)"}}>&#62;</h1></h2></button>
            </div>
        </div>
    )
}