import React, { useEffect, useState, useRef } from "react";
import { Link } from "react-router-dom";
import "../../styles/style-schedules.css"

export default function TrainSchedules(){
    const routes = 
    [
        [
            {
                pathName: "Belgrade - Bar(B1343)",
                busStops: [
                    "Belgrade",
                    "Poega",
                    "Uice",
                    "Bijelo Polje",
                    "Podgorica",
                    "Sutomore",
                    "Bar"
                ],
                schedule: {
                    to: {
                        scheduleRoutes: [
                            ["", "00:11", "00:59", "05:22", "07:50", "08:36", "08:49"], ["21:10", "00:30", "01:10", "05:45", "07:58", "8:39", ""]
                        ],
                    },
                    from: {
                        scheduleRoutes: [
                            ["", "20:10", "20:52", "23:03", "03:32", "04:08", "07:56"], ["20:00", "20:14", "21:05", "23:35", "03:45", "04:30", ""]
                        ],
                    },
                },
                exceptSunday: false
            },
            {
                pathName: "Belgrade - Bar(B1343)",
                busStops: [
                    "Belgrade",
                    "Valjevo",
                    "Poega",
                    "Uice",
                    "Priboj",
                    "Prijepolje",
                    "Bijelo Polje",
                    "Mojkovac",
                    "Kolain",
                    "Podgorica",
                    "Sutomore",
                    "Bar"
                ],
                schedule: {
                    to: {
                        scheduleRoutes: [
                            ["", "23:02", "00:28", "00:50", "02:46", "03:48", "05:22", "06:17", "06:35", "07:55", "08:46", "09:02"], ["21:10", "23:02", "00:29", "00:55", "02:47", "03:49", "05:52", "06:18", "06:36", "08:05", "08:50"]
                        ],
                    },
                    from: {
                        scheduleRoutes: [
                            ["", "19:12", "19:57", "21:29", "21:47", "22:13", "00:17", "01:19", "03:20", "03:46", "05:13", "07:06"], ["19:00", "19:16", "20:10", "21:30", "21:48", "22:43", "00:18", "01:20", "03:25", "03:47", "05:14"]
                        ],
                    },
                },
                exceptSunday: false
            }
        ]
    ]

    const [scheduleNumber, setScheduleNumber] = useState(0);
    const [routesArray, setRoutesArray] = useState(routes);
    const [idToEdit, setIdToEdit] = useState(undefined);
    const [isToButtonClicked, setIsToButtonClicked] = useState(true)
    const [isFromButtonClicked, setIsFromButtonClicked] = useState(false)

    console.log("is toButton clicked: " + isToButtonClicked)
    console.log("is fromButton clicked: " + isFromButtonClicked)

    function displayToPath(index){
        setIdToEdit(index);
        setIsToButtonClicked(true);
        setIsFromButtonClicked(false);
    }

    function displayFromPath(index){
        setIdToEdit(index)
        setIsToButtonClicked(false);
        setIsFromButtonClicked(true);
    }

    
    function changeColorTo(){
        if(isToButtonClicked)
            return "rgb(255, 131, 189)";
        else{
            return "rgb(38, 12, 24)";
        }
    }

    function changeColorFrom(){
        if(isFromButtonClicked)
            return "rgb(255, 131, 189)";
        else{
            return "rgb(38, 12, 24)";
        }
    }

    function reverseArray(array){
        array.reverse();
    }

    return (
        <div>
        <div style={{minWidth: "900px", width: "100%", marginInline: "auto", marginTop: "20px", fontSize: "1.4rem"}}>
            <div className="d-flex justify-content-center align-items-center" style={{flexWrap: "wrap"}}>
                {routesArray[scheduleNumber].map((routeObject, index) => {
                    let busStops = routeObject.busStops;
                    return (
                        <div className="schedule-card__wrapper">
                            {/*   */}
                            <div className="schedule-table__wrapper trainSchedule">
                                <h2 className="schedule-route-name">{routeObject.pathName}</h2>
                                <hr style={{color: "white", border: "2px solid white"}}/>
                                {routeObject.schedule.isEveryHour ? 
                                    <h4 className="schedule-route-name">This bus runs on the route every hour from 
                                        <span style={{borderBottom: "2px solid #408185"}}> {idToEdit !== index  ? routeObject.schedule.to.scheduleRoutes[0][0] : routeObject.schedule.from.scheduleRoutes[0][0]}</span> a.m. to <span style={{borderBottom: "2px solid #408185"}}>
                                        { idToEdit !== index ?
                                            routeObject.schedule.to.scheduleRoutes[0][0].replace(/\d+:\d+/g, () =>
                                            {
                                                let time = routeObject.schedule.to.scheduleRoutes[0][0];
                                                let hour = Number.parseInt(time);
                                                hour +=18;
                                                let newTime;
                                                console.log(newTime)
                                                if(hour == 24){
                                                    newTime = time.replace(/^\d+/g, "00");
                                                    return newTime
                                                }
                                                else if(hour == 25){
                                                    newTime = time.replace(/^\d+/g, "01")
                                                    return newTime
                                                }
                                                else{
                                                    newTime = time.replace(/^\d+/g, hour)
                                                    return newTime
                                                }
                                            })
                                            :
                                            routeObject.schedule.from.scheduleRoutes[0][0].replace(/\d+:\d+/g, () =>
                                            {
                                                let time = routeObject.schedule.from.scheduleRoutes[0][0];
                                                let hour = Number.parseInt(time);
                                                hour +=18;
                                                let newTime;
                                                console.log(newTime)
                                                if(hour == 24){
                                                    newTime = time.replace(/^\d+/g, "00");
                                                    return newTime
                                                }
                                                else if(hour == 25){
                                                    newTime = time.replace(/^\d+/g, "01")
                                                    return newTime
                                                }
                                                else{
                                                    newTime = time.replace(/^\d+/g, hour)
                                                    return newTime
                                                }
                                            })
                                        }
                                        </span> a.m.
                                    </h4> 
                                    : 
                                    <h4 style={{color: "white", textAlign: "center"}}>This train runs 1 time per day{routeObject.exceptSunday ? " except Sunday." : "."}</h4>
                                }
                                <table style={{marginBlock: "auto"}} className="d-flex justify-content-center">
                                    <tbody>
                                        <tr>
                                            <th></th>
                                            <th style={{color: "#23262a", backgroundColor: "#cca4b7", textAlign: "center"}}>Arrival</th>
                                            <th style={{color: "#23262a", backgroundColor: "#cca4b7", textAlign: "center"}}>Departure</th>
                                        </tr>
                                    {idToEdit !== index 
                                        ? busStops.map((busStop, busStopIndex) => {
                                        return <tr className="train-table-row">
                                            <th className="schedule-bus-name">{busStop}</th>
                                            {index === idToEdit 
                                                ? routeObject.schedule.from.scheduleRoutes.map((schedule)=><th className="schedule-cell">{schedule[busStopIndex]}</th>)
                                                : routeObject.schedule.to.scheduleRoutes.map((schedule)=><th className="schedule-cell">{schedule[busStopIndex]}</th>)}
                                        </tr>
                                        })
                                        : [...busStops].reverse().map((busStop, busStopIndex) => {
                                            return <tr className="train-table-row">
                                                <th className="schedule-bus-name">{busStop}</th>
                                                {index === idToEdit 
                                                    ? routeObject.schedule.from.scheduleRoutes.map((schedule)=><th className="schedule-cell">{schedule[busStopIndex]}</th>)
                                                    : routeObject.schedule.to.scheduleRoutes.map((schedule)=><th className="schedule-cell">{schedule[busStopIndex]}</th>)}
                                            </tr>
                                        })
                                    }
                                    </tbody>
                                    <div className="d-flex justify-content-center" style={{margin: "auto", marginLeft: "15px"}}>
                                        <button onClick={() => displayToPath(undefined)} className="schedule-table_button" ><h1 style={{color: idToEdit === index ? changeColorTo() : ""}} className="train-to-button" >&darr;</h1></button>
                                        <button onClick={() => displayFromPath(index)} className="schedule-table_button" ><h1 style={{color: idToEdit === index ? changeColorFrom() : ""}} className="train-from-button">&uarr;</h1></button>
                                    </div>
                                </table>
                            </div>
                        </div>
                    )
                })
            }
            </div>
        </div>
        <h3 style={{fontWeight: "bold", marginLeft: "10%", marginTop: "5%", display: "inline", padding: "10px", color: "rgb(194, 24, 91)"}}>If you need more information about the route, go <Link style={{borderBottom: "2px solid rgb(194, 24, 91)"}} to="/trainDetail"><span className="link-button">here</span></Link></h3>
    </div>
    ) 
}
