import React, { useState, useRef, useEffect } from "react";
import NavigationDropdownElement from "./NavigationDropdownElement";

export default function NavigationDropdownList(props) {
  const [listElements] = useState(props.listElements.elements);
  const [display, setDisplay] = useState("none");
  const dropdownRef = useRef();

  function handleClick() {
    setDisplay(display === "none" ? "block" : "none");
  }

  useEffect(() => {
    const onClick = (e) => {
      if (dropdownRef.current && !dropdownRef.current.contains(e.target)) {
        setDisplay("none");
      }
    };
    document.addEventListener("click", onClick);
    return () => document.removeEventListener("click", onClick);
  }, []);

  return (
    <li className="d-flex align-items-center z-1" style={{ marginInline: "10px" }}>
      <div className="fw-bold">
        <button
          ref={dropdownRef}
          className="navigation-list_button"
          style={{ padding: 0, textAlign: "center" }}
          onClick={handleClick}
        >
          {props.listName}
        </button>
        <ul
          className="navigation-dropdown-menu_list position-absolute"
          style={{
            display: display,
            backgroundColor: "white",
            border: "1px solid #00000054",
            borderRadius: "5px",
            paddingLeft: 0,
          }}
        >
          {listElements.map((element, index) => (
            <NavigationDropdownElement elementName={element} key={index} />
          ))}
        </ul>
      </div>
    </li>
  );
}
