import React, { useState, useRef, useEffect, createContext } from "react";
import DropdownElement from "./DropdownElement"
import ListContext from "../../contexts/ListContext"
import { Modal, ModalBody, ModalDialog, Button } from "react-bootstrap";

export default function DropdownList(props)
{
    const[listElements] = useState(props.listElements.elements);
    const[listTitle, setListTitle] = useState(props.listName);
    const[selectedPicture, setSelectedPicture] = useState(props.listElements.pictures[0])
    const[display, setDisplay] = useState("none");
    const dropdownRef = useRef();

    useEffect(() => {
        const onClick = e => dropdownRef.current.contains(e.target) || setDisplay("none");
        document.addEventListener('click', onClick);
        return () => document.removeEventListener('click', onClick);
      }, []);

      const [show, setShow] = useState(false);

      const handleClose = () => setShow(false);
      const handleShow = () => setShow(true);

    function handleClick(event)
    {
        if(event.target)
        {
            setDisplay("block")
        }
    }

    function updateListTitle(value){
        setListTitle(value)
    }

    function updateListPicture(value, picture){
        setSelectedPicture(picture);
        setListTitle(value)
    }

    return (
        <div className="d-flex align-items-center z-1">
            <Modal show={show} onHide={handleClose} closeButton>
                <Modal.Body style={{fontSize: "2rem", height: "25%"}}>
                    This feature is currently unavailable
                </Modal.Body>
            </Modal>
            <div className="language-menu fw-bold">
                <ListContext.Provider value={listTitle}>
                    <button ref={dropdownRef} onClick={handleClick}><p style={{display: "inline-block", margin: "15px 0", fontSize: "1.8rem"}}>{listTitle}</p><img style={{marginBottom: "15px"}} src={selectedPicture} className="img-fluid ms-2"></img></button>
                    <ul onClick={handleShow} className="list-group position-absolute" style={{display: display}}>
                        {listElements.map((element, index) => 
                                <DropdownElement selectedPicture={selectedPicture} updateListTitle={updateListTitle} updateListPicture={updateListPicture} elementPicture={props.listElements.pictures[index]} elementName={element} key={index} />
                            )
                        }
                    </ul>
                </ListContext.Provider>
            </div>
        </div>
    );
}
