import React from 'react';
import ReactDOM from 'react-dom/client';
import './index.css';
import Between from './Between';
import {
  createBrowserRouter,
  RouterProvider,
} from "react-router-dom";
import MapPage from './MapPage';
import ErrorPage from './ErrorPage';
import MunicipalityPage from './MunicipalityPage';
import SiteWidthContext from './contexts/SiteWidthContext';
import BetweenContent from './components/BetweenContent';
import TouristBus from './components/municipality/TouristBus'
import MunicipalityContent from './components/municipality/MunicipalityContent';
import Logistics from "./components/Logistics"
import Carouselle from './components/municipality/Carouselle';
import Taxi from './components/Taxi';
import Airport from './components/Airport';
import BusSchedules from './components/schedules/BusSchedules';
import FerrySchedules from './components/schedules/FerrySchedules';
import AirportSchedules from './components/schedules/AirportSchedules';
import TrainSchedules from './components/schedules/TrainSchedules';
import Ferryboat from './components/Ferryboat';
import Train from "./components/Train"
import POI from './components/municipality/POI';
import RouteBuilder from './components/RouteBuilder';
import FollowMe from './components/FollowMe';

const SiteLanguageContext = React.createContext();

const router = createBrowserRouter([
  {
    path: "/",
    element: <Between />,
    errorElement: <ErrorPage />,
    children: [
      {
        path: "map",
        element: <MapPage />,
      },
      {
        path: "main",
        element: <BetweenContent />,
      },
      {
        path: "bus",
        element: <BusSchedules />
      },
      {
        path: "logistics",
        element: <Logistics />,
      },
      {
        path: "ferryboat",
        element: <FerrySchedules />,
      },
      {
        path: "airport",
        element: <AirportSchedules />,
      },
      {
        path: "train",
        element: <TrainSchedules />,
      },
      {
        path: "taxi",
        element: <Taxi />,
      },
      {
        path: "airportDetail",
        element: <Airport />,
      },
      {
        path: "ferryboatDetail",
        element: <Ferryboat />
      },
      {
        path: "trainDetail",
        element: <Train />,
      },
      {
        path: "excursions",
        element: <POI />
      },
      {
        path: "follow-me",
        element: <FollowMe/>
      },
      {
        path: "routebuilder",
        element: <RouteBuilder/>
      },
      {
        path: "route-builder",
        element: <RouteBuilder />
      },
      {
        path: "municipalityDetail/*",
        element: <MunicipalityPage />,
        children: [
          {
              path: "touristsBus",
              element: <TouristBus />
          },
          {
            path: "kotor",
            element: <MunicipalityContent />
          },
          {
            path: "busPaths",
            element: <Carouselle />
          },
          {
            path: "POI",
            element: <POI />
          },
        ]
      }]
  },   
]);

const root = ReactDOM.createRoot(document.getElementById('root'));
root.render(
  <SiteWidthContext.Provider value="100%">
    <SiteLanguageContext.Provider value="ENG">
      <React.StrictMode>
        <RouterProvider router={router} />
      </React.StrictMode>
    </SiteLanguageContext.Provider>
  </SiteWidthContext.Provider>
);

export default SiteLanguageContext;