import React from "react";
import { Link } from "react-router-dom";

export default function Taxi(){
    return (
    <div style={{margin: "2%"}}>
        <div style={{textAlign: "center", marginBottom: "2%"}}>
            <h1 className="display-5" style={{backgroundColor: "#FFBB08", display: "inline-block", padding: "15px", borderRadius: "15px", fontWeight: "bold"}}>Kotor Taxi</h1>
        </div>
        <div style={{display:"flex", justifyContent:"space-evenly", alignItems: "center"}}>
            <div>
                <h1 style={{textAlign: "center"}}>Red taxi Kotor</h1>
                <p style={{textAlign: "center"}}><a href="https://www.redtaxikotor.com/"><img className="taxi-logo" src="/images/taxi/redtaxi-logo.png" alt="" /></a></p>
                <ul style={{fontSize: "2.7rem"}}>
                    <li className="li-telephone">+ 382 67 019 719</li>
                    <li className="li-telephone">+ 382 67 019 729</li>
                    <li className="li-internet">redtaxikotor.com</li>
                    <li className="li-mail">redtaxikotor@gmail.com</li>
                </ul>
            </div>
            <hr style={{ transform: 'rotate(90deg)', width: "250px",}}></hr>
            <div>
                <h1 style={{textAlign: "center"}}>Boka travel Kotor</h1>
                <p style={{textAlign: "center"}}><a href="https://www.bokatravel.com/"><img className="taxi-logo" src="/images/taxi/bokatravel-logo1.png" alt="" /></a></p>
                <ul style={{fontSize: "2.7rem"}}>
                    <li className="li-telephone">+ 382 69 788 722</li>
                    <li className="li-telephone">+ 382 67 511 109</li>
                    <li className="li-internet">bokatravel.com</li>
                    <li className="li-mail">info@bokatravel.com</li>
                </ul>
            </div>
        </div>
            <button className="text-button fs-2 fw-bold" style={{marginLeft: "4%"}}><Link to="/municipalityDetail/kotor"><span className="navigation-button">&#60;</span> Back to Kotor transport</Link></button>
    </div>
    )
}
