import React, { useEffect, useState, useRef } from "react";
import { Link } from "react-router-dom";
import "../../styles/style-schedules.css"

export default function FerrySchedules(){
    const routes = 
    [
        [
            {
                pathName: "Kamenari - Lepetane Winter",
                busStops: [
                    "",
                    "",
                    "",
                    "",
                    "",
                    "",
                    ""
                ],
                schedule: {
                    to: {
                        scheduleRoutes: [
                            ["00:00", "01:00", "02:00", "03:00", "04:00", "05:00", "06:00"]
                        ],
                    },
                    from: {
                        scheduleRoutes: [
                            ["0:30", "01:30", "02:30", "03:30", "04:30", "05:30", "06:30"]
                        ],
                    },
                },
                description: "every 15 minutes from 06:00 to 23:35"

            },
            {
                pathName: "Kamenari - Lepetane Summer",
                busStops: [
                    "",
                    "",
                    "",
                    "",
                    "",
                    "",

                ],
                schedule: {
                to: {
                    scheduleRoutes: [
                        []
                    ]
                },
                from: {
                    scheduleRoutes: [
                        []
                    ]
                }
                },
                descriptions: ["every 15 minutes from 00:00 to 05:45", "every 10 minutes from 06:00 to 23:50"],
            },

        ],
    ]

    const [scheduleNumber, setScheduleNumber] = useState(0);
    const [routesArray, setRoutesArray] = useState(routes);
    const [busStopArray] = useState(routes);
    const [idToEdit, setIdToEdit] = useState(undefined);
    const [isToButtonClicked, setIsToButtonClicked] = useState(true)
    const [isFromButtonClicked, setIsFromButtonClicked] = useState(false)

    function displayToPath(index){
        setIdToEdit(index);
        setIsToButtonClicked(true);
        setIsFromButtonClicked(false);
    }

    function displayFromPath(index){
        setIdToEdit(index)
        setIsToButtonClicked(false);
        setIsFromButtonClicked(true);
    }

    
    function changeColorTo(){
        if(isToButtonClicked)
            return "#26db77";
        else{
            return "rgb(7, 1, 31)";
        }
    }

    function changeColorFrom(){
        if(isFromButtonClicked)
            return "#26db77";
        else{
            return "rgb(7, 1, 31)";
        }
    }

    function reverseArray(array){
        array.reverse();
        console.log(array)
    }

    return (
        <div>
        <div style={{minWidth: "900px", width: "100%", marginInline: "auto", marginTop: "20px", fontSize: "1.4rem"}}>
            <div className="d-flex justify-content-center align-items-center" style={{flexWrap: "wrap"}}>
                {routesArray[scheduleNumber].map((routeObject, index) => {
                    let busStops = routeObject.busStops;
                    return (
                        <div className="schedule-card__wrapper" style={{maxWidth: "445px"}}>
                            {/*   */}
                            <div className="schedule-table__wrapper ferrySchedule">
                                <h2 className="schedule-route-name">{routeObject.pathName}</h2>
                                <hr style={{color: "white", border: "2px solid white"}}/>
                                <table style={{marginBlock: "auto"}} className="d-flex justify-content-center">
                                    <tbody>
                                        <tr>
                                            <th style={{textAlign: "center", backgroundColor: "#8ad5ac"}}>Night</th>
                                            <th style={{textAlign: "center", backgroundColor: "#8ad5ac"}}>Day</th>
                                        </tr>
                                        {routeObject.schedule.to.scheduleRoutes[0].length !== 0 
                                        ? <>{idToEdit !== index
                                            ? 
                                            <tr className="feryy-table-row">  
                                                <tr className="feryy-table-row"><th>00:00</th></tr>
                                                <tr className="feryy-table-row"><th>01:00</th></tr>
                                                <tr className="feryy-table-row"><th>02:00</th></tr>
                                                <tr className="feryy-table-row"><th>03:00</th></tr>
                                                <tr className="feryy-table-row"><th>04:00</th></tr>
                                                <tr className="feryy-table-row"><th>05:00</th></tr>
                                                <tr className="feryy-table-row"><th>06:00</th></tr>
                                                <th style={{padding: "5px", backgroundColor: "#d7e9df"}}>{routeObject.description}</th>
                                            </tr>
                                            : 
                                            <tr className="feryy-table-row">  
                                                <tr className="feryy-table-row"><th>00:30</th></tr>
                                                <tr className="feryy-table-row"><th>01:30</th></tr>
                                                <tr className="feryy-table-row"><th>02:30</th></tr>
                                                <tr className="feryy-table-row"><th>03:30</th></tr>
                                                <tr className="feryy-table-row"><th>04:30</th></tr>
                                                <tr className="feryy-table-row"><th>05:30</th></tr>
                                                <tr className="feryy-table-row"><th>06:30</th></tr>
                                                <th style={{padding: "5px", backgroundColor: "#d7e9df"}}>{routeObject.description}</th>
                                            </tr>
                                        } </>
                                        :
                                            <tr>
                                                <th style={{padding: "5px", backgroundColor: "#d7e9df", textAlign: "center"}}>{routeObject.descriptions[0]}</th>
                                                <th style={{padding: "5px", backgroundColor: "#d7e9df", textAlign: "center"}}>{routeObject.descriptions[1]}</th>
                                            </tr>
                                        }
                                    </tbody>
                                </table>
                            </div>
                        </div>
                    )
                })
            }
            </div>
        </div>
        <h2 style={{fontWeight: "bold", padding: "10px", color: "rgb(9, 113, 56)", textAlign: "center"} }><span style={{borderBottom: "2px solid rgb(0, 77, 35)"}}>Travel time 15 minutes</span></h2>
        <div>
            <h3 style={{fontWeight: "bold", marginLeft: "10%", padding: "10px", color: "rgb(9, 113, 56)"}}>If you need more information about the route, go <Link style={{borderBottom: "2px solid #408185"}} to="/ferryboatDetail"><span className="link-button">here</span></Link></h3>
        </div>
    </div>
    ) 
}
