import React, { useEffect, useState, useRef } from "react";
import { Link } from "react-router-dom";
import "../../styles/style-schedules.css"

export default function AirportSchedules(){

    const [departuresDisplay, setDeparturesDisplay] = useState(true);
    const [arrivalsDisplay, setArrivalsDisplay] = useState(false)

    const toButton = useRef();
    const fromButton = useRef();

    function displayToPath(){
        setDeparturesDisplay(true);
        setArrivalsDisplay(false);
        toButton.current.style.color = "#699cff"
        fromButton.current.style.color = "rgb(90, 101, 117)"
    }

    function displayFromPath(){
        setArrivalsDisplay(true);
        setDeparturesDisplay(false);
        toButton.current.style.color = "rgb(90, 101, 117)"
        fromButton.current.style.color = "#699cff"
    }

    return (
        <div style={{height: "70vh"}}>
        <div style={{minWidth: "900px", width: "100%", marginInline: "auto", marginTop: "20px", fontSize: "1.4rem"}}>
            <div className="d-flex justify-content-center align-items-center" style={{flexWrap: "wrap"}}>
                <div className="schedule-card__wrapper">
                    <div className="schedule-table__wrapper airportSchedule">
                        <h2 className="schedule-route-name">Airport Tivat flights</h2>
                        <hr style={{color: "white", border: "2px solid white"}}/>
                        <table style={{marginBlock: "auto"}} className="d-flex justify-content-center">
                            <table className="air-tab" style={{display: departuresDisplay ? "table" : "none"}}>
                                <tr className="schedule-airport">
                                    <th style={{textAlign: "center"}}>Flight</th>
                                    <th style={{textAlign: "center"}}>Time</th>
                                    <th style={{textAlign: "center"}}>Destination</th>
                                    <th style={{textAlign: "center"}}>Terminal</th>
                                    <th style={{textAlign: "center"}}>Aircraft</th>
                                    <th style={{textAlign: "center"}}>Status</th>
                                </tr>
                                <tr className="schedule-airport">
                                    <th className="air-th">MNE400</th>
                                    <th className="air-th">--:--</th>
                                    <th className="air-th">Istabul</th>
                                    <th className="air-th">T1</th>
                                    <th className="air-th">E190</th>
                                    <th className="air-th">------</th>
                                </tr>
                                <tr className="schedule-airport">
                                    <th className="air-th">JU181</th>
                                    <th className="air-th">--:--</th>
                                    <th className="air-th">Belgrade</th>
                                    <th className="air-th">T1</th>
                                    <th className="air-th">A320/A319/ATR 72</th>
                                    <th className="air-th">------</th>
                                </tr>
                                <tr className="schedule-airport">
                                    <th className="air-th">JU183</th>
                                    <th className="air-th">--:--</th>
                                    <th className="air-th">Belgrade</th>
                                    <th className="air-th">T1</th>
                                    <th className="air-th">A320/A319/ATR 72</th>
                                    <th className="air-th">------</th>
                                </tr>
                                <tr className="schedule-airport">
                                    <th className="air-th">MNE152</th>
                                    <th className="air-th">--:--</th>
                                    <th className="air-th">Belgrade</th>
                                    <th className="air-th">T1</th>
                                    <th className="air-th">E190</th>
                                    <th className="air-th">------</th>
                                </tr>
                            </table>
                        <table className="air-tab" style={{display: arrivalsDisplay ? "table" : "none"}}>
                            <tr className="schedule-airport">
                                <th style={{textAlign: "center"}}>Flight</th>
                                <th style={{textAlign: "center"}}>Time</th>
                                <th style={{textAlign: "center"}}>Origin</th>
                                <th style={{textAlign: "center"}}>Terminal</th>
                                <th style={{textAlign: "center"}}>Aircraft</th>
                                <th style={{textAlign: "center"}}>Status</th>
                            </tr>
                            <tr className="schedule-airport">
                                <th className="air-th">MNE153</th>
                                <th className="air-th">--:--</th>
                                <th className="air-th">Belgrade</th>
                                <th className="air-th">T1</th>
                                <th className="air-th">E190</th>
                                <th className="air-th">------</th>
                            </tr>
                            <tr className="schedule-airport">
                                <th className="air-th">JU180</th>
                                <th className="air-th">--:--</th>
                                <th className="air-th">Belgrade</th>
                                <th className="air-th">T1</th>
                                <th className="air-th">A320/A319/ATR 72</th>
                                <th className="air-th">------</th>
                            </tr>
                            <tr className="schedule-airport">
                                <th className="air-th">JU182</th>
                                <th className="air-th">--:--</th>
                                <th className="air-th">Belgrade</th>
                                <th className="air-th">T1</th>
                                <th className="air-th">A320/A319/ATR 72</th>
                                <th className="air-th">------</th>
                            </tr>
                            <tr className="schedule-airport">
                                <th className="air-th">MNE401</th>
                                <th className="air-th">--:--</th>
                                <th className="air-th">Istabul</th>
                                <th className="air-th">T1</th>
                                <th className="air-th">E190</th>
                                <th className="air-th">------</th>
                            </tr>
                        </table>
                        <div className="d-flex justify-content-center" style={{margin: "auto", marginLeft: "15px"}}>
                            <button className="schedule-table_button" onClick={displayToPath}><h1 ref={toButton} className="airport-to-button">&darr;</h1></button>
                            <button className="schedule-table_button" onClick={displayFromPath}><h1 ref={fromButton} className="airport-from-button">&uarr;</h1></button>
                        </div>
                        </table>
                    </div>
                </div>
            </div>
        </div>
        <h3 style={{fontWeight: "bold", marginLeft: "10%", marginTop: "10%", display: "inline", padding: "10px", color: "#0D0D9E"}}>If you need more information, go <Link style={{borderBottom: "2px solid #0D0D9E"}} to="/airportDetail"><span className="link-button">there</span></Link></h3>
    </div>
    ) 
}
