import React from "react";
import NavigationMenu from "./UI/NavigationMenu";

export default function BetweenTopNavigation(){
    return (
        <NavigationMenu width="80%" marginInline="auto" fontSize="1.5rem" margin-top="15px" navigationElements={{navigations: [
            {
                name: "Map",
                className: "map-element",
                isDropdown: false,
                elements: []
            }, 
            {
                name: "Schedules",
                className: "schedules-element",
                isDropdown: true,
                buttons: ["Bus", "Ferryboat", "Airport", "Train"]
            }, 
            {
                name: "Destinations",
                className: "destinations-element",
                isDropdown: true,
                buttons: ["Logistics", "Excursions"]
            },
            {
                name: "Services",
                className: "services-element",
                isDropdown: true,
                buttons: ["Route builder", "Follow me"]
            },
        ]}
        
        } />
    );
}


