import React, { useEffect, useRef, useState } from "react";
import { Link } from "react-router-dom";

function Airport() {
    const [isPictureVisible, setIsPictureVisible] = useState(false);
    const [activeTab, setActiveTab] = useState("departures");
    const showMapButton = useRef();

    const handleDeparturesClick = () => {
        setActiveTab("departures");

    };

    const handleArrivalsClick = () => {
        setActiveTab("arrivals");

    };
    
    const handleTogglePicture = () => {
        setIsPictureVisible(!isPictureVisible);
        // isPictureVisible ? showMapButton.current.style.transform = "translateY(0px)" : showMapButton.current.style.transform = "translateY(50px)";
    };


  return (
    <div>
        <div style={{display: "flex", justifyContent:"center", alignItems: "center", fontSize: "28px",margin:"40px  0px 0px 0px"}}>
            <div style={{display: "flex"}}>
                <div style={{display:"flex", justifyContent: "space-between"}}>
                    <img style={{width: "150px",height: "800px",marginTop: "10px",border: "solid", boxShadow: "10px 10px 0px darkblue", marginRight: "40px"}} src="/images/tivatAirport.jpg" alt="" />
                    {isPictureVisible && (
                        <div style={{alignSelf: "flex-end"}}>
                            <img style={{width: '400px', height: '250px', border: 'solid', boxShadow: "5px 5px 0px darkblue"}} src="/images/airportBulding.png" alt="" />
                        </div>
                    )}
                </div>
            </div>
            <div>
              <div style={{textAlign: "center", marginBottom: "20px"}}><h2 className="bg-title darkblue">Tivat Airport (TIV)</h2></div>
              <div style={{display:"flex", justifyContent:"center"}}>
                  <button
                  className="section-button"
                  style={{
                      background: activeTab === "departures" ? "darkblue" : "rgb(235, 235, 235)",
                      color: activeTab === "departures" ? "white" : "darkblue",
                      fontWeight: "bold"
                  }}
                  onClick={handleDeparturesClick}
                  >
                  Departures
                  </button>
                  <button
                  className="section-button"
                  style={{
                      background: activeTab === "arrivals" ? "darkblue" : "rgb(235, 235, 235)",
                      color: activeTab === "arrivals" ? "white" : "darkblue",
                      fontWeight: "bold"
                  }}
                  onClick={handleArrivalsClick}
                  >
                  Arrivals
                  </button>
              </div>
            {activeTab === "departures" ? (
              <table className="air-tab" style={{minWidth: "780px"}}>
                <tr style={{ color: "while" }}>
                  <th style={{textAlign: "center"}}>Flight</th>
                  <th style={{textAlign: "center"}}>Time</th>
                  <th style={{textAlign: "center"}}>Destination</th>
                  <th style={{textAlign: "center"}}>Terminal</th>
                  <th style={{textAlign: "center"}}>Aircraft</th>
                  <th style={{textAlign: "center"}}>Status</th>
                </tr>
                <tr className="air-tr">
                  <th className="air-th">MNE400</th>
                  <th className="air-th">--:--</th>
                  <th className="air-th">Istanbul</th>
                  <th className="air-th">T1</th>
                  <th className="air-th">E190</th>
                  <th className="air-th">------</th>
                </tr>
                <tr className="air-tr">
                  <th className="air-th">JU181</th>
                  <th className="air-th">--:--</th>
                  <th className="air-th">Belgrade</th>
                  <th className="air-th">T1</th>
                  <th className="air-th">A320/A319/ATR 72</th>
                  <th className="air-th">------</th>
              </tr>
                <tr className="air-tr">
                  <th className="air-th">JU183</th>
                  <th className="air-th">--:--</th>
                  <th className="air-th">Belgrade</th>
                  <th className="air-th">T1</th>
                  <th className="air-th">A320/A319/ATR 72</th>
                  <th className="air-th">------</th>
                </tr>
                <tr className="air-tr">
                  <th className="air-th">MNE152</th>
                  <th className="air-th">--:--</th>
                  <th className="air-th">Belgrade</th>
                  <th className="air-th">T1</th>
                  <th className="air-th">E190</th>
                  <th className="air-th">------</th>
                </tr>
              </table>
            ) : (
              <table className="air-tab" style={{minWidth: "780px"}}>
                <tr style={{ color: "while"}}>
                  <th style={{textAlign: "center"}}>Flight</th>
                  <th style={{textAlign: "center"}}>Time</th>
                  <th style={{textAlign: "center"}}>Origin</th>
                  <th style={{textAlign: "center"}}>Terminal</th>
                  <th style={{textAlign: "center"}}>Aircraft</th>
                  <th style={{textAlign: "center"}}>Status</th>
                </tr>
                <tr className="air-tr">
                  <th className="air-th">MNE153</th>
                  <th className="air-th">--:--</th>
                  <th className="air-th">Belgrade</th>
                  <th className="air-th">T1</th>
                  <th className="air-th">E190</th>
                  <th className="air-th">------</th>
              </tr>
              <tr className="air-tr">
                  <th className="air-th">JU180</th>
                  <th className="air-th">--:--</th>
                  <th className="air-th">Belgrade</th>
                  <th className="air-th">T1</th>
                  <th className="air-th">A320/A319/ATR 72</th>
                  <th className="air-th">------</th>
              </tr>
              <tr className="air-tr">
                  <th className="air-th">JU182</th>
                  <th className="air-th">--:--</th>
                  <th className="air-th">Belgrade</th>
                  <th className="air-th">T1</th>
                  <th className="air-th">A320/A319/ATR 72</th>
                  <th className="air-th">------</th>
              </tr>
              <tr className="air-tr">
                  <th className="air-th">MNE401</th>
                  <th className="air-th">--:--</th>
                  <th className="air-th">Istabul</th>
                  <th className="air-th">T1</th>
                  <th className="air-th">E190</th>
                  <th className="air-th">------</th>
              </tr>
              </table>)}
              <table className="air-tab" style={{margin:"5% auto", width: "100%"}}>
                  <tr className="air-row">
                      <th className="air-cell" style={{textAlign: "center"}}>Nearest bus stop<img src="/images/icon/busIcon.png"/></th>
                      <th className="air-cell" style={{textAlign: "center"}}>Local taxi<img src="/images/icon/taxiIcon.png"/></th>
                      <th className="air-cell" style={{textAlign: "center"}}>Nearest ferryboat<img src="/images/icon/ferryIcon.png"/></th>
                  </tr>
                  <tr> 
                      <th className="air-cell" style={{textAlign: "center"}}>Airport Tivat</th>
                      <th className="air-cell" style={{textAlign: "center"}}>RedTaxi | BokaTravel</th>
                      <th className="air-cell" style={{textAlign: "center"}}>Lepetane</th>
                  </tr>
              </table>
              <div style={{display: "flex", justifyContent: "space-between"}}>
                <button className="text-button fs-2 fw-bold" style={{marginTop:"8%"}}><Link to="/municipalityDetail/kotor"><span className="navigation-button">&#60;</span> Back to Kotor transport</Link></button>
                <button ref={showMapButton} style={{background:"darkblue", color:"white", borderRadius: "15px", padding: "8px", alignSelf:"flex-end", width: "35%", minWidth: "273px"}} onClick={handleTogglePicture}>
                      {isPictureVisible ? 'Close' : 'Map of the building'}
                  </button>
              </div>                
              </div>
        </div>
    </div>
    )
}

export default Airport