import React, { useContext } from "react";
import ListContext from "../../contexts/ListContext";

export default function DropdownElement(props)
{
    const listTitle = useContext(ListContext);
    const handleClick = () => {
        props.updateListTitle(props.elementName);
        props.updateListPicture(props.elementName, props.elementPicture);
    }
    const handleImageClick = (e) => {
        e.stopPropagation();
        props.updateListTitle(props.elementName);
        props.updateListPicture(props.elementName, props.elementPicture);
    }

    return (
        <li className="list-group-item align-items-center" style={{padding: "0.5rem",display:"flex",justifyContent:"space-between"}} onClick={handleClick}>
            {props.elementName}
            <img src={props.elementPicture} style={{marginLeft: "15px"}}/>
        </li>
    );
}
