import {Link} from "react-router-dom"

export default function NavigationDropdownElement(props)
{
    let elName = props.elementName.toLowerCase().replace(" ", "-");

    return (
        <li className={"navigation-menu_element dropdown-button " + elName + "-navigation"}>
            <Link className="" to={elName}>
                <div className="d-flex justify-content-between">
                    <button style={{padding: "10px", color: "inherit"}} >{props.elementName}</button>
                    <div style={{paddingTop: "5%", paddingRight: "15px", fontSize: "2rem"}}>&gt;</div>
                </div>
            </Link>
        </li>
    );
}