import React, {useState} from "react";
import NavigationDropdownList from "./NavigationDropdownList";
import NavigationElement from "./NavigationElement";

export default function NavigationMenu({...props}){
    const[navMenu] = useState(props.navigationElements.navigations);

    return (
        <div className="d-flex justify-content-between" style={{...props}}>
            <ul className="nav nav-tabs card-header-tabs">
                {navMenu.map((navObject, index) => {
                    if(!navObject.isDropdown)
                        return (
                            <NavigationElement className={navObject.className} elementName={navObject.name} key={index}/>
                        )    
                    else{
                        return (
                            <NavigationDropdownList key={index} listName={navObject.name} 
                                listElements={{
                                    elements: navObject.buttons, 
                                }}/>
                            )  
                        }
                    }
                )}
            </ul>
        </div>
    );
}


