// react
import React, { useState, useEffect, useRef } from 'react';

// openlayers
import Map from 'ol/Map'
import View from 'ol/View'
import TileLayer from 'ol/layer/Tile'
import OSM from 'ol/source/OSM.js';
import {transform} from 'ol/proj'

function MapWrapper(props) {

  // set intial state
  const [ map, setMap ] = useState()
  const [ featuresLayer, setFeaturesLayer ] = useState()
  const [ selectedCoord , setSelectedCoord ] = useState()

  // pull refs
  const mapElement = useRef()
  const mapRef = useRef()
  mapRef.current = map

  const montenegroCoords = transform([19.336065, 42.6682022], "EPSG:4326", "EPSG:900913")

  useEffect( () => {
    // create map
    const initialMap = new Map({
        target: mapElement.current,
        layers: [

            // USGS Topo
            new TileLayer({
            source: new OSM()
            }),
        ],
        view: new View({
            projection: 'EPSG:3857',
            center: montenegroCoords,
            zoom: 8.5,
            maxZoom: 20,
            minZoom: 8.5,
        }),
        controls: []
    })

    // set map onclick handler
    initialMap.on('click', handleMapClick)

    // save map and vector layer references to state
    // setMap(initialMap)

  },[])


  // map click handler
  const handleMapClick = (event) => {

    const clickedCoord = mapRef.current.getCoordinateFromPixel(event.pixel);

    // transform coord to EPSG 4326 standard Lat Long
    const transormedCoord = transform(clickedCoord, 'EPSG:3857', 'EPSG:4326')

    // set React state
    setSelectedCoord( transormedCoord )

    console.log(transormedCoord)
    
  }

  // render component
  return (
    <div className="container mt-5" style={{borderStyle:"solid"}}>
        <div ref={mapElement} className="map-container" style={{height: "800px"}}></div>
    </div>)

}

export default MapWrapper