import React, { useState } from "react";
import { Link } from "react-router-dom";

function Airport() {
    const [activeTab, setActiveTab] = useState("departures");

    const handleDeparturesClick = () => {
        setActiveTab("departures");};

    const handleArrivalsClick = () => {
        setActiveTab("arrivals");};

  return (
    <div>
      <div style={{display: "flex", justifyContent:"center", alignItems: "center", fontSize: "28px",margin:"40px  0px 0px 0px"}}>
          <div style={{display: "flex", flexDirection: "column", marginRight: "70px"}}>
              <div style={{display:"flex", alignItems:"flex-end"}}>
                  <img style={{width:"150px",height:"600px",marginTop:"10px",border:"solid", boxShadow: "10px 10px 0px #C2185B"}} src="/images/train.png" alt="" />
              </div>
          </div>
          <div>
          <div style={{color:"white", textAlign:"center", marginBottom: "20px"}}><h2 className="bg-title burgundy">Belgrade - Bar</h2></div>
          <div style={{display:"flex", justifyContent:"center"}}>
              <button
                className="section-button"
                  style={{
                      fontWeight:"bold",
                      background: activeTab === "departures" ? "rgb(194, 24, 91)" : "#EBEBEB",
                      color: activeTab === "departures" ? "white" : "rgb(194, 24, 91)",
                  }}onClick={handleDeparturesClick}>
                  Departures
              </button>
              <button
                  className="section-button"
                  style={{
                      fontWeight:"bold",
                      background: activeTab === "arrivals" ? "rgb(194, 24, 91)" : "#EBEBEB",
                      color: activeTab === "arrivals" ? "white" : "rgb(194, 24, 91)",
                  }} onClick={handleArrivalsClick}>
                  Arrivals
              </button>
          </div>
        {activeTab === "departures" ? (
          <table className="tr-tab">
            <tr style={{color: "while" }}>
              <th style={{textAlign: "center"}}>Number</th>
              <th style={{textAlign: "center"}}>Stops</th>
              <th style={{textAlign: "center"}}>Time</th>
              <th style={{textAlign: "center"}}>Destination</th>
              <th style={{textAlign: "center"}}>Terminal</th>
              <th style={{textAlign: "center"}}>Train</th>
              <th style={{textAlign: "center"}}>Status</th>
            </tr>
            <tr className="">
              <th className="air-th">B 432 </th>
              <th className="air-th">12</th>
              <td className="air-th">--:--</td>
              <td className="air-th" style={{fontWeight: "bold"}}>Belgrade Main Railway station</td>
              <th className="air-th">1</th>
              <th className="air-th">Loven</th>
              <th className="air-th">------ </th>
            </tr>
            <tr className="air-tr">
              <th className="air-th">B 1342</th>
              <th className="air-th">7</th>
              <th className="air-th">--:--</th>
              <th className="air-th" style={{fontWeight: "bold"}}>Belgrade Main Railway station</th>
              <th className="air-th">1</th>
              <th className="air-th">Auto voz</th>
              <th className="air-th">------ </th>
          </tr>
          </table>
        ) : (
          <table className="tr-tab" style={{minWidth: "905px"}}>
            <tr style={{color: "while" }}>
              <th style={{textAlign: "center"}}>Number</th>
              <th style={{textAlign: "center"}}>Stops</th>
              <th style={{textAlign: "center"}}>Time</th>
              <th style={{textAlign: "center"}}>Destination</th>
              <th style={{textAlign: "center"}}>Terminal</th>
              <th style={{textAlign: "center"}}>Train</th>
              <th style={{textAlign: "center"}}>Status</th>
            </tr>
            <tr className="">
              <th className="air-th">B 1343</th>
              <th className="air-th">7</th>
              <th className="air-th">--:--</th>
              <th className="air-th">Bar</th>
              <th className="air-th">1</th>
              <th className="air-th">Auto voz</th>
              <th className="air-th">------ </th>
            </tr>
            <tr className="air-tr">
              <th className="air-th">B 433 </th>
              <th className="air-th">12</th>
              <td className="air-th">--:--</td>
              <td className="air-th">Bar</td>
              <th className="air-th">1</th>
              <th className="air-th">Loven</th>
              <th className="air-th">------ </th>
          </tr>
          </table>)}
          <table style={{margin:"5% auto", minWidth: "657px"}}>
                  <tr className="air-row" >
                      <th className="air-cell" style={{textAlign: "center"}}>Nearest bus stop<img src="/images/icon/busIcon.png"/></th>
                      <th className="air-cell" style={{textAlign: "center"}}>Local taxi<img src="/images/icon/taxiIcon.png"/></th>
                      <th className="air-cell" style={{textAlign: "center"}}>Nearest airport<img src="/images/icon/airportIcon.png"/></th>
                  </tr>
                  <tr >
                      <th className="air-cell" style={{textAlign: "center"}}>-------</th>
                      <th className="air-cell" style={{textAlign: "center"}}>-------</th>
                      <th className="air-cell" style={{textAlign: "center"}}>-------</th>
                  </tr>
          </table>
            <button className="text-button fs-2 fw-bold" style={{marginTop:"8%"}}><Link to="/municipalityDetail/kotor"><span className="navigation-button">&#60;</span> Back to Kotor transport</Link></button>
          </div>
      </div>
    </div>
    )
}

export default Airport