import React, { useEffect, useState, useRef } from "react";
import { Link } from "react-router-dom";
import "../../styles/style-schedules.css"

export default function BusSchedules(){
    const routes = 
    [
        [
            {
                pathName: "Kotor - Mirac",
                busStops: [
                    "Kotor Bus station",
                    "kaljari",
                    "Trojica",
                    "Tvrava Gorada",
                    "Mirac"
                ],
                schedule: {
                    to: {
                    name: "Kotor",
                    scheduleRoutes: [
                        ["6:30", "6:40", "6:50", "7:00", "7:10"], ["13:30", "13:40", "13:50", "14:00", "14:10"]
                    ],
                    },
                    from: {
                    name: "Mirac",
                    scheduleRoutes: [
                        ["7:30", "7:40", "7:50", "8:00", "8:10"], ["14:30", "14:40", "14:50", "15:00", "15:10"]
                    ],
                    },
                },
                exceptSunday: false

            },
            {
                pathName: "Kotor - Gorovii",
                busStops: [
                "Kotor Old town",
                "Kotor Bus station",
                "kaljari",
                "Trojica",
                "Dub",
                "Sutvara",
                "Naljeii",
                "iii",
                "Brateii",
                "Gorovii",
                ],
                schedule: {
                to: {
                    name: "Kotor",
                    scheduleRoutes: [
                        ["13:45", "13:50", "13:55", "14:01", "14:05", "14:09", "14:13", "14:17", "14:21", "14:25"]
                    ]
                },
                from: {
                    name: "Gorovici",
                    scheduleRoutes: [
                        ["6:25", "6:29", "6:33", "6:37", "6:41", "6:45", "6:49", "6:55", "7:00", "7:05"]
                    ]
                }
                },
                exceptSunday: false
            },
            {
                pathName: "Kotor - Kava",
                busStops: [
                "Kotor Bus station",
                "kaljari",
                "Trojica",
                "Kava"
                ],
                schedule: {
                to: {
                    name: "Kotor",
                    scheduleRoutes: [
                        ["13:50", "14:00", "14:08", "14:20"]
                    ]
                },
                from: {
                    name: "Kavac",
                    scheduleRoutes: [
                        ["7:10", "7:22", "7:30", "7:40"]
                    ]
                }
                },
                exceptSunday: true
            },
            {
                pathName: "Kotor - Krimovica",
                busStops: [
                    "Kotor Bus station",
                    "Riva",
                    "Jugodrvo",
                    "Ljesevici",
                    "Bigova",
                    "Vranovici",
                    "Pobrdje",
                    "Glavatici",
                    "Kubasi",
                    "Savina",
                    "Krimovica"
                ],
                schedule: {
                    to: {
                        name: "Kotor",
                        scheduleRoutes: [
                            ["13:50", "13:55", "14:04", "14:11", "14:20", "14:31", "14:35", "14:41", "14:45", "14:52", "14:59" ]
                        ]
                    },
                    from: {
                        name: "Krimovica",
                        scheduleRoutes: [
                            ["6:30", "6:37", "6:44", "6:48", "6:54", "6:58", "7:09", "7:18", "7:25", "7:34", "7:39"]
                        ]
                    }
                },
                exceptSunday: true
            }
        ],
        [
            {
                pathName: "kaljari - Kostanjica",
                busStops: [
                "kaljari",
                "Kotor Bus station",
                "Kotor Old Town",
                "Dobrota",
                "Ljuta",
                "Orahovac",
                "Drain",
                "Vrt",
                "Perast",
                "Risan",
                "Strp",
                "Lipci",
                "Morinj",
                "Kostanjica"
                ],
                schedule: {
                    to: {
                    name: "kaljari",
                    scheduleRoutes: 
                        [
                            ["6:15", "6:19", "6:23", "6:27", "6:31", "6:35", "6:39", "6:43", "6:47", "6:51", "6:55", "6:59", "7:03", "7:05"],
                        ],
                    },
                    from: {
                    name: "Kostanica",
                    scheduleRoutes: 
                        [
                            ["7:15", "7:19", "7:23", "7:27", "7:31", "7:35", "7:39", "7:43", "7:47", "7:51", "7:55", "7:59", "8:03", "8:05"],
                        ],
                    },
                    isEveryHour: true,
                },
                exceptSunday: false
            },
            {
                pathName: "Kamp - Stoliv",
                busStops: [
                "Kamp",
                "Dobrota",
                "Kotor Old Town",
                "Mio",
                "Pranj",
                "Stoliv"
                ],
                schedule: {
                    to: {
                        name: "Kamp",
                        scheduleRoutes: [["6:15", "6:23", "6:31", "6:39", "6:47", "6:55"]]
                    },
                    from: {
                        name: "Stoliv",
                        scheduleRoutes: [["7:00", "7:08", "7:16", "7:24", "7:32", "7:40"]]
                    },
                isEveryHour: true,
                },
                exceptSunday: false
            },
            {
                pathName: "kaljari - Dragalj",
                busStops: [
                "kaljari",
                "Kotor Bus station",
                "Kotor Old Town",
                "RK Kamelija",
                "Dobrota",
                "Ljuta",
                "Orahovac",
                "Perast",
                "Risan",
                "Strp",
                "Dragalj"
                ],
                schedule: {
                    to: {
                        name: "Skaljari",
                        scheduleRoutes: [["12:30", "12:33", "12:36", "12:38", "12:40", "12:58", "13:01", "13:09", "13:15", "13:19", "14:00"]]
                    },
                    from: {
                        name: "Dragalj",
                        scheduleRoutes: [["7:00", "7:41", "7:45", "7:51", "7:59", "8:02", "8:20", "8:22", "8:24", "8:27", "8:30"]]
                    }   
                },
                exceptSunday: false
            },
            {
                pathName: "Kotor - Tivat airport",
                busStops: [
                "Kotor Old Town",
                "Kotor Bus station ",
                "Skaljari",
                "E80 Mrsulja",
                "Airport Tivat"
                ],
                schedule: {
                    to: {
                        name: "Kotor",
                        scheduleRoutes: [["6:00", "6:05", "6:10", "6:20", "6:30"]]
                    },
                    from: {
                        name: "Tivat airport",
                        scheduleRoutes: [["7:00", "7:10", "7:20", "7:25", "7:30"]]
                    },
                isEveryHour: true,
                },
                exceptSunday: false
            }
        ],
    ]

    const [scheduleNumber, setScheduleNumber] = useState(0);
    const [routesArray, setRoutesArray] = useState(routes);
    const [busStopArray] = useState(routes);
    const [idToEdit, setIdToEdit] = useState(undefined);
    const [isToButtonClicked, setIsToButtonClicked] = useState(true)
    const [isFromButtonClicked, setIsFromButtonClicked] = useState(false)

    const nextButton = useRef();
    const prevButton = useRef();

    const toButton = useRef();
    const fromButton = useRef();

    console.log("is toButton clicked: " + isToButtonClicked)
    console.log("is fromButton clicked: " + isFromButtonClicked)

    function displayToPath(index){
        setIdToEdit(index);
        setIsToButtonClicked(true);
        setIsFromButtonClicked(false);
    }

    function displayFromPath(index){
        setIdToEdit(index)
        setIsToButtonClicked(false);
        setIsFromButtonClicked(true);
    }

    
    function changeColorTo(){
        if(isToButtonClicked)
            return "rgb(76, 178, 184)";
        else{
            return "rgb(3, 32, 34)";
        }
    }

    function changeColorFrom(){
        if(isFromButtonClicked)
            return "rgb(76, 178, 184)";
        else{
            return "rgb(3, 32, 34)";
        }
    }

    function setPreviousScheduleNumber(){
        if(scheduleNumber > 0)
        {
            let newIndex = scheduleNumber - 1;
            setScheduleNumber(newIndex);
            setIdToEdit(undefined)
            prevButton.current.style.display = "none";
            nextButton.current.style.display = "block";
        }
    }

    function setNextScheduleNumber(){
        if(scheduleNumber < routes.length - 1)
        {
            let newIndex = scheduleNumber + 1;
            setScheduleNumber(newIndex);
            setIdToEdit(undefined)
            prevButton.current.style.display = "block";
            nextButton.current.style.display = "none";
        }
    }

    function reverseArray(array){
        array.reverse();
        console.log(array)
    }

    return (
        <div>
        <div style={{minWidth: "900px", width: "100%", marginInline: "auto", marginTop: "20px", fontSize: "1.4rem"}}>
            <div className="d-flex justify-content-center align-items-center" style={{flexWrap: "wrap"}}>
                {routesArray[scheduleNumber].map((routeObject, index) => {
                    let busStops = routeObject.busStops;
                    return (
                        <div className="schedule-card__wrapper" style={{maxWidth: "445px"}}>
                            {/*   */}
                            <div className="schedule-table__wrapper busSchedule">
                                <h2 className="schedule-route-name">{routeObject.pathName}</h2>
                                <hr style={{color: "white", border: "2px solid white"}}/>
                                {routeObject.schedule.isEveryHour ? 
                                    <h4 className="schedule-route-name">This bus runs on the route every hour from 
                                        <span style={{borderBottom: "2px solid #408185"}}> {idToEdit !== index  ? routeObject.schedule.to.scheduleRoutes[0][0] : routeObject.schedule.from.scheduleRoutes[0][0]}</span> a.m. to <span style={{borderBottom: "2px solid #408185"}}>
                                        { idToEdit !== index ?
                                            routeObject.schedule.to.scheduleRoutes[0][0].replace(/\d+:\d+/g, () =>
                                            {
                                                let time = routeObject.schedule.to.scheduleRoutes[0][0];
                                                let hour = Number.parseInt(time);
                                                hour +=18;
                                                let newTime;
                                                console.log(newTime)
                                                if(hour == 24){
                                                    newTime = time.replace(/^\d+/g, "00");
                                                    return newTime
                                                }
                                                else if(hour == 25){
                                                    newTime = time.replace(/^\d+/g, "01")
                                                    return newTime
                                                }
                                                else{
                                                    newTime = time.replace(/^\d+/g, hour)
                                                    return newTime
                                                }
                                            })
                                            :
                                            routeObject.schedule.from.scheduleRoutes[0][0].replace(/\d+:\d+/g, () =>
                                            {
                                                let time = routeObject.schedule.from.scheduleRoutes[0][0];
                                                let hour = Number.parseInt(time);
                                                hour +=18;
                                                let newTime;
                                                console.log(newTime)
                                                if(hour == 24){
                                                    newTime = time.replace(/^\d+/g, "00");
                                                    return newTime
                                                }
                                                else if(hour == 25){
                                                    newTime = time.replace(/^\d+/g, "01")
                                                    return newTime
                                                }
                                                else{
                                                    newTime = time.replace(/^\d+/g, hour)
                                                    return newTime
                                                }
                                            })
                                        }
                                        </span> a.m.
                                    </h4> 
                                    : 
                                    <h4 style={{color: "white", textAlign: "center"}}>This bus runs 1 time per day{routeObject.exceptSunday ? " except Sunday." : "."}</h4>
                                }
                                <table style={{marginBlock: "auto"}} className="d-flex justify-content-center">
                                    <tbody>
                                    {idToEdit !== index 
                                        ? busStops.map((busStop, busStopIndex) => {
                                        return <tr className="bus-table-row">
                                            <th className="schedule-bus-name">{busStop}</th>
                                            {index === idToEdit 
                                                ? routeObject.schedule.from.scheduleRoutes.map((schedule)=><th className="schedule-cell">{schedule[busStopIndex]}</th>)
                                                : routeObject.schedule.to.scheduleRoutes.map((schedule)=><th className="schedule-cell">{schedule[busStopIndex]}</th>)}
                                        </tr>
                                        })
                                        : [...busStops].reverse().map((busStop, busStopIndex) => {
                                            return <tr className="bus-table-row">
                                                <th className="schedule-bus-name">{busStop}</th>
                                                {index === idToEdit 
                                                    ? routeObject.schedule.from.scheduleRoutes.map((schedule)=><th className="schedule-cell">{schedule[busStopIndex]}</th>)
                                                    : routeObject.schedule.to.scheduleRoutes.map((schedule)=><th className="schedule-cell">{schedule[busStopIndex]}</th>)}
                                            </tr>
                                        })
                                    }
                                    </tbody>
                                    <div className="d-flex justify-content-center" style={{margin: "auto", marginLeft: "15px"}}>
                                        <button onClick={() => displayToPath(undefined)} className="schedule-table_button" ><h1 style={{color: idToEdit === index ? changeColorTo() : ""}} className="bus-to-button">&darr;</h1></button>
                                        <button onClick={() => displayFromPath(index)} className="schedule-table_button" ><h1 style={{color: idToEdit === index ? changeColorFrom() : ""}} className="bus-from-button">&uarr;</h1></button>
                                    </div>
                                </table>
                            </div>
                        </div>
                    )
                })
            }
            </div>
            <div className="d-flex schedule-buttons justify-content-around" style={{marginTop: "50px"}}>
                <button ref={prevButton}  style={{display: "none"}} onClick={setPreviousScheduleNumber}>
                    <h3 className="fw-bold"><span className="navigation-button">&#60;</span> Previous schedule</h3>
                </button>
                <button ref={nextButton} className="" onClick={setNextScheduleNumber}>
                    <h3 className="fw-bold">Next schedule<span className="navigation-button">&#62;</span></h3>
                </button>
            </div>
        </div>
        <h3 style={{fontWeight: "bold", marginLeft: "10%", marginTop: "5%", display: "inline", padding: "10px", color: "rgb(0, 87, 92)"}}>If you need more information about the route, go <Link style={{borderBottom: "2px solid #408185"}} to="/municipalityDetail/busPaths"><span className="link-button">here</span></Link></h3>
    </div>
    ) 
}

