import { Link } from "react-router-dom";
import BetweenHeader from "./components/BetweenHeader";
import BetweenTopNavigation from "./components/BetweenTopNavigation";
import SiteWidthContext from './contexts/SiteWidthContext.jsx';

export default function ErrorPage()
{
    const SiteWidth = SiteWidthContext;

    return (
        <SiteWidth.Consumer >
            { value => (
            <div className="App m-auto" style={{width: value}}>
                <BetweenHeader />
                <hr />
                <BetweenTopNavigation />
                <div className="container text-center d-flex h-100 align-items-center flex-column">
                    <div className="display-3">Oops!</div>
                    <div className="h3 mt-3">The page address is entered <span style={{color: "red", textDecorationLine: "underline"}}>incorrectly</span>!</div>
                    <div className="h3">Do you want to go <Link to="/main"><span style={{textDecorationLine: "underline"}} className="letter-yellow">back</span></Link> to the main page?</div>
                </div>
            </div>
            )}
        </SiteWidth.Consumer>
    );
}