import React from "react";
import { Link } from "react-router-dom";

export default function NavigationElement(props)
{
    return (
        <li className="navigation-list_button" style={{marginInline: "10px"}}>
            <Link className={" " + props.className} to={props.elementName.toLowerCase()} style={{color: "black"}}>
                {props.elementName}
            </Link>
        </li>
    );
}