import { Modal, ModalBody, ModalDialog, Button } from "react-bootstrap";
import { useState } from "react";

export default function FollowMe(){

    const [show, setShow] = useState(false);

    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);

    return(
        <div className="container" style={{marginTop: "25px"}}>
            <Modal show={show} onHide={handleClose} closeButton>
                <Modal.Body style={{fontSize: "2rem", height: "25%"}}>This feature is currently unavailable!</Modal.Body>
            </Modal>
            <div style={{backgroundColor: "#33333342", padding: "30px", borderRadius: "15px", fontWeight: "bold"}}>
                <h1 className="display-4" ><span className="follow-me_text">Welcome to <span style={{color: "#FFBB08", borderRadius: "10px", padding: "5px", backgroundColor: "#333333", borderBottomWidth: "4px", borderBottomStyle: "solid", borderBottomColor: "#FFBB08"}}>Follow me</span></span></h1>
                <h3 className="follow-me_text">We can get you to your destination quickly and safely - just trust us and follow the directions our system will give you.</h3>
                <h3 className="follow-me_text">First, fill in the fields with the data that we will request, then click <span style={{color: "#FFBB08", backgroundColor: "#333333", padding: "8px", borderRadius: "10px"}}>start following</span>. If appropriate, the system will offer several route options, and when you decide on the priority for you and select it, the system will immediately begin route guidance.</h3>
                <h3 className="follow-me_text" style={{}}><span style={{borderBottomStyle: "solid", borderBottomColor: "#F5B46B"}}>Please note that the more complete data you provide, the less time you can spend on the journey between the points of departure and arrival.</span></h3>
                <div style={{display: "flex", justifyContent: "center"}}>
                    <button onClick={handleShow} style={{color: "#FFBB08", backgroundColor: "#333333", padding: "10px", borderRadius: "35px", width: "30%"}} className="follow-me_start-button">
                        <h2>Start following <h1 style={{display: "inline-block", transform: "translateY(8px)"}}>&#62;</h1></h2>
                    </button>
                </div>
            </div>
        </div>
    )
}