import React, { useEffect, useRef } from "react";
import {Link} from "react-router-dom"
import Carousel, {activeIndex} from "react-bootstrap/Carousel";
import { useState } from "react";

// ,     - )
// Man who will clean it up - sorry)

export default function Carouselle()
{

    const routes = [
        {
            pathName: "Kotor - Mirac",
            busStops: [
              "Kotor Bus station",
              "kaljari",
              "Trojica",
              "Tvrava Gorada",
              "Mirac"
            ],
            schedule: {
              to: {
                name: "Kotor",
                scheduleRoutes: [["6:30", "6:40", "6:50", "7:00", "7:10"], ["13:30", "13:40", "13:50", "14:00", "14:10"]],
              },
              from: {
                name: "Mirac",
                scheduleRoutes: [["7:30", "7:40", "7:50", "8:00", "8:10"], ["14:30", "14:40", "14:50", "15:00", "15:10"]],
              }
            }
          },
        {
          pathName: "Kotor - Gorovii",
          busStops: [
            "Kotor Old town",
            "Kotor Bus station",
            "kaljari",
            "Trojica",
            "Dub",
            "Sutvara",
            "Naljeii",
            "iii",
            "Brateii",
            "Gorovii",
          ],
          schedule: {
            to: {
              name: "Kotor",
              scheduleRoutes: [["13:45", "13:50", "13:55", "14:01", "14:05", "14:09", "14:13", "14:17", "14:21", "14:25"]]
            },
            from: {
              name: "Gorovii",
              scheduleRoutes: [["6:25", "6:29", "6:33", "6:37", "6:41", "6:45", "6:49", "6:55", "7:00", "7:05"]]
            }
          }
        },
        {
          pathName: "Kotor - Kava",
          busStops: [
            "Kotor Bus station",
            "kaljari",
            "Trojica",
            "Kava"
          ],
          schedule: {
            to: {
              name: "Kotor",
              scheduleRoutes: [["13:50", "14:00", "14:08", "14:20"]]
            },
            from: {
              name: "Kava",
              scheduleRoutes: [["7:10", "7:22", "7:30", "7:40"]]
            }
          }
        },
        {
          pathName: "kaljari - Kostanjica",
          busStops: [
            "kaljari",
            "Kotor Bus station",
            "Kotor Old Town",
            "Dobrota",
            "Ljuta",
            "Orahovac",
            "Drain",
            "Vrt",
            "Perast",
            "Risan",
            "Strp",
            "Lipci",
            "Morinj",
            "Kostanjica"
            ],
            schedule: {
              to: {
                name: "kaljari",
                scheduleRoutes: [
                  ["6:15", "6:19", "6:23", "6:27", "6:31", "6:35", "6:39", "6:43", "6:47", "6:51", "6:55", "6:59", "7:03", "7:05"],
                ],
                description: ["This bus goes every hour", "from 06:15 to 00:15"]
              },
              from: {
                name: "Kostanjica",
                scheduleRoutes: [
                  ["7:15", "7:19", "7:23", "7:27", "7:31", "7:35", "7:39", "7:43", "7:47", "7:51", "7:55", "7:59", "8:03", "8:05"],
                ],
                description: ["This bus goes every hour", "from 07:15 to 02:05"]
              },
            }
        },
        {
          pathName: "Kotor - Krimovica",
          busStops: [
            "Kotor Bus station",
            "Riva",
            "Jugodrvo",
            "Ljesevici",
            "Bigova",
            "Vranovici",
            "Pobrdje",
            "Glavatici",
            "Kubasi",
            "Savina",
            "Krimovica"
          ],
          schedule: {
            to: {
              name: "Kotor",
              scheduleRoutes: [["13:50", "13:55", "14:04", "14:11", "14:20", "14:31", "14:35", "14:41", "14:45", "14:52", "14:59" ]]
            },
            from: {
              name: "Krimovica",
              scheduleRoutes: [["6:30", "6:37", "6:44", "6:48", "6:54", "6:58", "7:09", "7:18", "7:25", "7:34", "7:39"]]
            }
          }
        },
        {
          pathName: "Kamp - Stoliv",
          busStops: [
            "Kamp",
            "Dobrota",
            "Kotor Old Town",
            "Mio",
            "Pranj",
            "Stoliv"
          ],
          schedule: {
            to: {
              name: "Kamp",
              scheduleRoutes: [["6:15", "6:23", "6:31", "6:39", "6:47", "6:55"]],
              description: ["This bus goes is every hour", "from 6:15 to 00:15"]
            },
            from: {
              name: "Stoliv",
              scheduleRoutes: [["7:00", "7:08", "7:16", "7:24", "7:32", "7:40"]],
              description: ["This bus goes is every hour", "from 7:15 to 01:00"]
            },
          }
        },
        {
          pathName: "kaljari - Dragalj",
          busStops: [
            "kaljari",
            "Kotor Bus station",
            "Kotor Old Town",
            "RK Kamelija",
            "Dobrota",
            "Ljuta",
            "Orahovac",
            "Drajin Vrt",
            "Perast",
            "Risan",
            "Strp",
            "Lipci",
            "Dragalj"
          ],
          schedule: {
            to: {
              name: "kaljari",
              scheduleRoutes: [["12:30", "12:33", "12:36", "12:38", "12:40", "12:58", "13:01", "13:05", "13:09", "13:15", "13:19", "13:24", "14:00"]]
            },
            from: {
              name: "Dragalj",
              scheduleRoutes: [["7:00", "7:36", "7:41", "7:45", "7:51", "7:56", "7:59", "8:02", "8:20", "8:22", "8:24", "8:27", "8:30"]]
            }
          }
        },
        {
          pathName: "Kotor - Airport Tivat",
          busStops: [
            "Kotor Old Town",
            "Kotor Bus station ",
            "kaljari",
            "E80 Mrsulja",
            "Airport Tivat"
          ],
          schedule: {
            to: {
              name: "Kotor",
              scheduleRoutes: [["6:00", "6:05", "6:10", "6:20", "6:30"]],
              description: ["This bus goes is every 30 minutes", "from 6:00 to 00:00"]
            },
            from: {
              name: "Airport Tivat",
              scheduleRoutes: [["7:00", "7:10", "7:20", "7:25", "7:30"]],
              description: ["This bus goes is every 30 minutes", "from 7:00 to 01:00"]
            },
          }
        }
      ]

    const [index, setIndex] = useState(0);
    const [busRoute, setBusRoute] = useState(routes[index].busStops)
    const [route, setRoute] = useState(null);
    const [scheduleDisplay, setScheduleDisplay] = useState("none",);
    const [schedule, setSchedule] = useState(routes[0].schedule);
    const [toPathDisplay, setToPathDisplay] = useState("flex");
    const [fromPathDisplay, setFromPathDisplay] = useState("none");
    const [descriptionDisplay, setDescriptionDisplay] = useState("none")
    const scheduleRef = useRef();
    const scheduleWrapper = useRef();
    const toPathButton = useRef();
    const fromPathButton = useRef();

    useEffect(() =>
    {
        if(index === 0)
            setRoute("Kotor - Mirac")
        if(index === 1)
            setRoute("Kotor - Gorovii")
        if(index === 2)
            setRoute("Kotor - Kava")
        if(index === 3)
            setRoute("kaljari - Kostanjica")
        if(index === 4)
            setRoute("Kotor - Krimovica")
        if(index === 5)
            setRoute("Kamp - Stoliv")
        if(index === 6)
            setRoute("kaljari - Dragalj")
        if(index === 7)
            setRoute("Kotor - Airport Tivat")
    }, [index])
    
    useEffect(() => {
        if(index < 8){
            setSchedule(routes[index].schedule);
            setBusRoute(routes[index].busStops);
        }
    }, [index]);

    useEffect(() => {
      const onClick = e => scheduleWrapper.current.contains(e.target) || handleScheduleClose();
      document.addEventListener('click', onClick);
      return () => document.removeEventListener('click', onClick);
    }, []);

    const handleSelect = (selectedIndex, e) => {
        setIndex(selectedIndex);
    };

    let busStops = routes[index].busStops;

    function handleScheduleClose()
    {
      hideSchedule();
      showToPath();
      setDescriptionDisplay("none");
      scheduleRef.current.style.color = "black"
    }

    function renderSchedule()
    {
      setScheduleDisplay("flex");
      scheduleRef.current.style.color = "#00575C"
    }

    function hideSchedule()
    {
      setScheduleDisplay("none")
    }

    function showToPath(){
      setToPathDisplay("flex");
      setFromPathDisplay("none");
      toPathButton.current.style.color = "#00575C";
      fromPathButton.current.style.color = "black"
    }

    function showFromPath(){
      setToPathDisplay("none");
      setFromPathDisplay("flex");
      toPathButton.current.style.color = "black";
      fromPathButton.current.style.color = "#00575C";
    }

    return (
        <div  className="d-flex justify-content-center flex-column align-items-center" style={{marginTop: "20px", width: "100%", right: "150px"}} >
          {/* Schedule */}
          {/* End of Schedule */}
            <div ref={scheduleWrapper} className="d-flex container justify-content-between">
                <div className="carousel__wrapper" >
                    <Carousel activeIndex={index} onSelect={handleSelect} interval={null} >
                        <Carousel.Item>
                            <img
                                className="d-block w-100"
                                src="/images/routes/kotor-mirac.png"
                                alt="First slide"
                            />
                        </Carousel.Item>
                        <Carousel.Item>
                            <img
                                className="d-block w-100"
                                src="/images/routes/kotor-grovici.png"
                                alt="Second slide"
                            />
                        </Carousel.Item>
                        <Carousel.Item>
                            <img
                                className="d-block w-100"
                                src="/images/routes/kotor-kavac.png"
                                alt="Third slide"
                            />
                        </Carousel.Item>
                        <Carousel.Item>
                            <img
                                className="d-block w-100"
                                src="/images/routes/kotor-kostanica.png"
                                alt="Third slide"
                            />
                        </Carousel.Item>
                        <Carousel.Item>
                            <img
                                className="d-block w-100"
                                src="/images/routes/kotor-krimovica.png"
                                alt="Third slide"
                            />
                        </Carousel.Item>
                        <Carousel.Item>
                            <img
                                className="d-block w-100"
                                src="/images/routes/kamp-stoliv.png"
                                alt="Third slide"
                            />
                        </Carousel.Item>
                        <Carousel.Item>
                            <img
                                className="d-block w-100"
                                src="/images/routes/skalari-dragal.png"
                                alt="Third slide"
                            />
                        </Carousel.Item>
                        <Carousel.Item>
                            <img
                                className="d-block w-100"
                                src="/images/routes/kotor-tivatAirport.png"
                                alt="Third slide"
                            />
                        </Carousel.Item>
                    </Carousel>
                </div>
                <div className="d-flex flex-column" style={{marginLeft: "100px", fontSize: "25px", width: "550px"}}>
                    <div style={
                      {
                        textAlign: "center",
                        fontSize: "2rem", 
                        marginTop: "30px", 
                        backgroundColor: "#00575C", 
                        padding: "10px", 
                        borderRadius: "10px",
                        color: "white"
                      }
                    }>
                      {(index + 1)}/8: {route}
                    </div>
                    <div className="">
                        <h1>Bus stops</h1>
                        <div className="d-flex">
                            <ul style={{width: "80%"}}>
                                {fromPathDisplay == "flex" ? [...busRoute].reverse().map((ostanovka, index) => <li className="bus-stop_element" key={index}>{ostanovka}</li>) :  busRoute.map((ostanovka, index) => <li className="bus-stop_element" key={index}>{ostanovka}</li>)}
                            </ul>
                            <div>
                            <div className="justify-content-center align-items-center schedule-menu"
                                style={
                                    {
                                        display: scheduleDisplay
                                    }
                                }>
                                  {/* From Kotor */}
                                  <div className="d-flex" style={{width: "100%"}}>
                                    <div style={{textAlign: "center", display: toPathDisplay}}>
                                        {
                                          schedule.to.scheduleRoutes.map((subArray, subArrayIndex) => {
                                            return <ul key={subArrayIndex}>
                                                {busStops.map((busStop, busStopIndex) => {
                                                    return <li key={busStopIndex} className="schedule-list_element">{subArray[busStopIndex]}</li>
                                                })}
                                            </ul>
                                          })
                                        }
                                      </div>
                                      {/* To Kotor */}
                                      <div style={{textAlign: "center", display: fromPathDisplay}}>
                                        {
                                          schedule.from.scheduleRoutes.map((subArray, subArrayIndex) => {
                                            return <ul key={subArrayIndex}>
                                                {busStops.map((busStop, busStopIndex) => {
                                                    return <li key={busStopIndex} className="schedule-list_element">{subArray[busStopIndex]}</li>
                                                })}
                                            </ul>
                                          })
                                        }
                                      </div>
                                    <div className="d-flex justify-content-around" style={{width: "100px"}}>
                                        <button ref={toPathButton} className="schedule-menu_button to-button" onClick={showToPath}>&darr;</button>
                                        <button ref={fromPathButton} className="schedule-menu_button from-button" onClick={showFromPath}>&uarr;</button>
                                    </div>
                                </div>
                              </div>
                            </div>
                        </div>
                        
                        {/* <div className="align-self-center" style={{textAlign: "end", display: descriptionDisplay}}>{schedule.isEveryHour && (toPathDisplay == "flex" || fromPathDisplay == "flex")  ? "This bus goes every hour" : ""}</div> */}
                        
                        <div className="align-self-center" style={{textAlign: "end", display: descriptionDisplay}}>{toPathDisplay === "flex" && schedule.to.description !== undefined ? 
                          <div>
                            <div>{schedule.to.description[0]}</div>
                            <div>{schedule.to.description[1]}</div>
                          </div> 
                          : schedule.from.description !== undefined ?
                          <div>
                            <div>{schedule.from.description[0]}</div>
                            <div>{schedule.from.description[1]}</div>
                          </div> :
                          ""
                          }
                        </div>

                        <hr />
                        <div className="d-flex">
                          <button button className="text-button fs-4 flex-grow-1 fw-bold align-self-start back-to-kotor-button"><Link to="/municipalityDetail/kotor"><span className="navigation-button">&#60;</span> Back to Kotor transport</Link></button>
                          <div className="d-flex flex-column">
                              <button className="text-button schedule-button d-inline" style={{marginTop: "5px"}} onClick={e => {
                                e.stopPropagation()
                                renderSchedule()
                                setDescriptionDisplay("block")
                                }}>
                                <h4 ref={scheduleRef} className="fw-bold">Route Schedule<span className="navigation-button">&#62;</span></h4></button>
                              <button className="text-button schedule-button d-inline"><Link to="/municipalityDetail/poi"><h4 className="fw-bold" style={{marginRight:"-5px"}}>POI on the route<span className="navigation-button">&#62;</span></h4></Link></button>
                          </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}