import './styles/style.css';
import './styles/adaptive.css'
import BetweenHeader from './components/BetweenHeader.jsx';
import BetweenTopNavigation from './components/BetweenTopNavigation.jsx';
import SiteWidthContext from './contexts/SiteWidthContext.jsx';
import { Outlet } from 'react-router-dom';
import BetweenFooter from './components/BetweenFooter';

function Between(props) {
  const SiteWidth = SiteWidthContext;

  return (
      <SiteWidth.Consumer >
        { value => (
          <div className="App m-auto d-flex flex-column justify-content-between" style={{width: value}}>
              <BetweenHeader siteLanguage={props.language}/>
              <div className='d-flex'>
                <div className='header-point'><img className='header-point_image' src="/images/icon/place.png" alt="" /></div>
                <hr className='yellow-line'/>
                <hr className='red-line'/>
              </div>
              <BetweenTopNavigation />
              <Outlet />
              <BetweenFooter />
          </div>
        )}
      </SiteWidth.Consumer>
  );
}

export default Between;
