import React from "react";
import DropdownList from "./UI/DropdownList";
import { Link } from "react-router-dom";
import SiteLanguageContext from "..";
import moment from "moment/moment";
import { useState, useEffect } from "react";

export default function BetweenHeader(props){
    const LanguageContext = SiteLanguageContext;
    const [now, setNow] = useState(moment().utcOffset(60).format('H:mm')); 

    useEffect(() => {
        setInterval(() => setNow(moment().utcOffset(60).format('H:mm')), 1000);
      }, []);

    return (
        <div style={{width: "80%"}} className="m-auto">
            <div className="d-flex justify-content-between">
                <div className="d-flex flex-grow-1">
                    <Link style={{color: "inherit", textDecoration: "none"}} to="/main"><h1 className="d-inline fw-bold me-3 display-3">betw<span className="letter-yellow">e</span><span className="letter-red">e</span>n</h1></Link>
                    <div className="d-block align-self-center fs-4">
                        You set a target<br/>
                        everything else in between
                    </div>
                </div>
                <div style={{alignSelf: "center", fontSize: "1.8rem", marginRight: "15px"}}>
                    {now}
                </div>
                <LanguageContext.Consumer>
                    {value => (
                        <DropdownList listName={value} 
                        listElements={ {
                            elements: [value, "MNE", "SRB"], 
                            pictures: ["https://www.countryflagicons.com/FLAT/48/GB.png", "https://www.countryflagicons.com/FLAT/48/ME.png", "https://www.countryflagicons.com/FLAT/48/RS.png"]
                        }}/>
                    )}
                </LanguageContext.Consumer>
            </div>
        </div>      
    );
}
